* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #635097;
  top: 15px;
  left: 475px;
  height: 200px;
  width: 500px;
  position: fixed;
}
.head {
  color: whitesmoke;
  padding-left: 40px;
  padding-top: 50px;
}

.card {
  position: absolute;
  overflow: auto;
  top: 80px;
  margin-top: 30px;
  width: 500px;
  height: 820px;
  background: #ececec;
  border-radius: 60px;
}
.card a {
  text-decoration: none;
}

.cardfirst {
  position: relative;
  top: 50px;
  margin-left: 45px;
  margin-bottom: 20px;
  width: 400px;
  height: 270px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 1px black;
}
.Upper-container {
  height: 150px;
  background-color: white;
}
.image-container img {
  background: white;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-top: 15px;
  margin-left: 18px;
}
img {
  border: 1px Solid;
}

.Upper-container h2 {
  color: #635097;
  margin-top: -90px;
  margin-left: 150px;
}
.Upper-container h3 {
  margin-left: 150px;
  font-weight: normal;
}
.lower-container {
  background-color: #f5ecff;
  height: 120px;
}
.lower-container h3 {
  margin-left: 10px;
  font-weight: normal;
}
.lower-container h2 {
  color: #635097;
  margin-left: 10px;
}

.link {
  font-weight: normal;
  text-decoration: none;
}

.footer {
  width: 500px;
  height: 50px;
  background-color: #ececec;
  position: fixed;
  bottom: 0px;
  padding-bottom: 0px;
}
.footer p {
  padding-right: 40px;
}

